@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";
:root {
  font-size: 16px;
}
body {
  font-family: "Roboto", sans-serif;
}
.wrapper {
  max-width: 1440px;
  margin: 0 auto;
}
a {
  text-decoration: none;
}
.section-filter {
  margin-top: 40px;
  margin-bottom: 123px;
  @media (max-width: 320px) {
    margin-top: 20px;
    margin-bottom: 55px;
  }
}
.form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  &-slider {
    margin-bottom: 47px;
  }
}

.filter {
  text-align: center;
  &-buttons {
    text-align: center;
    margin-bottom: 15px;
    @media (max-width: 320px) {
      margin-bottom: 0;
    }
  }
  &-button {
    position: relative;
    margin-left: -4px;
    color: $black;
    background-color: $write;
    border: 1px solid $black;
    width: 320px;
    height: 58px;
    transition: 0.2s ease;
    outline: none;
    cursor: pointer;
    &::before {
      content: "";
      position: absolute;
      top: calc(50% - 4px);
      right: 86px;
      background-image: url("data:img/png;base64,");
      background-repeat: no-repeat;
      background-position: cover;
      width: 18px;
      height: 9px;
      @media (max-width: 320px) {
        background-size: 10px 5px;
        top: calc(50% - 2px);
        right: 80px;
      }
    }
    &__2 {
      &::before {
        right: 108px;
        @media (max-width: 320px) {
          right: 95px;
        }
      }
    }
    &:focus {
      &::before {
        background-image: url("data:img/png;base64,");
      }
    }
    @media (max-width: 320px) {
      margin-left: 0px;
      width: 290px;
      height: 40px;
      margin-bottom: 10px;
      font-size: 0.75rem;
    }
  }
}
.form-filter {
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 10px;
  @media (max-width: 320px) {
    flex-direction: column;
    margin-bottom: 0;
  }
  &__text {
    border: 1px solid $black;
    padding-left: 85px;
    padding-right: 65px;
    margin-right: 30px;
    @media (max-width: 320px) {
      font-size: 0.75rem;
      padding-left: 0;
      padding-right: 0;
      width: 290px;
      margin: 0 auto;
      margin-bottom: 10px;
    }
    &:last-child {
      margin-right: 0;
      padding-right: 53px;
      padding-left: 71px;
      @media (max-width: 320px) {
        padding-right: 0;
        padding-left: 0;
        margin: 0 auto;
      }
    }
  }
  &__input {
    width: 82px;
    height: 56px;
    border: none;
    border-left: 1px solid $black;
    outline: none;
    text-align: right;
    padding-left: 69px;
    margin-left: 83px;
    cursor: pointer;
    &::placeholder {
      color: $black;
      text-align: center;
    }
    @media (max-width: 320px) {
      width: 47px;
      height: 36px;
      padding-left: 40px;
      margin-left: 33px;
    }
  }
  &-button {
    position: relative;
    margin-left: -4px;
    color: $black;
    background-color: $write;
    border: none;
    width: 248px;
    height: 58px;
    transition: 0.2s ease;
    outline: none;
    cursor: pointer;
    @media (max-width: 320px) {
      width: 170px;
      height: 50px;
      margin: 0 auto;
    }
    &__reset {
      color: $gray;
      @media (max-width: 320px) {
        padding-left: 0;
        margin-bottom: 12px;
      }
      &::before {
        content: "";
        position: absolute;
        top: calc(50% - 4px);
        right: 34px;
        background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAeUlEQVQYlY2PUQqAIBBEx+5jmIFn8gAewwP01QE6ShBBFzJGNkHZjwbUdXg6u8ZZvwPY7uc6oWiZ1wAgToQAJDE6iZcqU0qBsz446w+evGue4Yb+dZZfa/211MABphpEscdfauAQzdUP+HsYFmPjWgqjowZR4mUA8QV5JVB9yGn3tAAAAABJRU5ErkJggg==");
        background-repeat: no-repeat;
        background-position: cover;
        width: 10px;
        height: 10px;
        @media (max-width: 320px) {
          right: 0px;
        }
      }
      &:hover {
        color: $black;
      }
    }
    &__submit {
      font-size: 1.12rem;
      font-weight: 700;
      color: $write;
      background-color: $gray;
      width: 220px;
      letter-spacing: 3px;
      @media (max-width: 320px) {
        font-size: 0.875rem;
      }
      &:hover {
        background-color: $black;
      }
    }
  }
}
.form-slider {
  //оформление ползунков
  display: flex;
  justify-content: center;
  @media (max-width: 320px) {
    display: none;
  }
  &__mob {
    @media (min-width: 321px) {
      display: none;
    }
    @media (max-width: 320px) {
      display: flex;
      margin: 18px auto;
      max-width: 300px;
    }
  }
  &__text {
    margin: 0;
    margin-top: 10px;
    font-size: 0.75rem;
  }
  span {
    margin-left: 130px;
  }
  &__input[type="range"] {
    position: relative;
    width: 365px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    overflow: hidden;
    height: 6px;
    &::-webkit-slider-runnable-track {
      height: 1px;
      background-color: $lightGray;
    }
    &::-webkit-slider-thumb {
      background: $black;
      cursor: pointer;
      width: 6px;
      height: 6px;
      -webkit-appearance: none;
      margin-top: -3px;
      box-shadow: -365px 0px 0px 365px $gray;
    }
    &::-moz-range-track {
      background-color: $lightGray;
      height: 1px;
    }
    &::-moz-range-thumb {
      background: $black;
      cursor: pointer;
    }
  }
} //конец оформления ползунков
.section-house {
  margin-bottom: 150px;
}
.start-block {
  text-align: center;
  &-title {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 35px;
    @media (max-width: 320px) {
      font-size: 1.25rem;
      margin-bottom: 15px;
    }
  }
  &-text {
    max-width: 680px;
    margin: 0 auto;
    line-height: 1.5;
    margin-bottom: 75px;
    @media (max-width: 320px) {
      font-size: 0.75rem;
      max-width: 290px;
      br {
        display: none;
      }
    }
  }
}
.slider {
  &-child {
    outline: none;
    box-sizing: border-box;
    margin: 0 10px;
  }
}
.slider-house {
  border: 1px solid $gray;
  width: 460px;
  height: 544px;
  box-sizing: border-box;
}
.slider-main {
