  }
  &__apartament1 {
    background-image: url(../img/apartaments/apartament-1.png);
  }
  &__apartament2 {
    background-image: url(../img/apartaments/apartament-2.png);
  }
  &__apartament3 {
    background-image: url(../img/apartaments/apartament-3.png);
  }
  &__apartament4 {
    background-image: url(../img/apartaments/apartament-4.png);
  }
  &__apartament5 {
    background-image: url(../img/apartaments/apartament-5.png);
  }
  &__apartament6 {
    background-image: url(../img/apartaments/apartament-6.png);
  }
}
.slider-info {
  position: relative;
  text-align: left;
  padding-top: 303px;
  padding-left: 30px;
  &__title {
    color: $write;
    font-size: 1.875rem;
    font-weight: 700;
    padding-left: 5px;
    padding-top: 5px;
  }
  &__text {
    color: $write;
    margin-top: 11px;
  }
  &__link {
    position: absolute;
    right: 30px;
    bottom: -2px;
    outline: none;
    border: none;
    background-image: url("data:img/png;base64,");
    background-repeat: no-repeat;
    background-position: center;
    width: 18px;
    height: 15px;
    background-color: $black;
    padding: 20px;
    &:hover {
      background-color: $gray;
    }
  }
}
.slider-footer {
  display: flex;
  margin-left: 62px;
  margin-top: 18px;
  color: $black;
  &-text {
    position: relative;
    &__1 {
      margin-right: 60px;
      &::before {
        content: "";
        position: absolute;
        right: 67px;
        bottom: 4px;
        background-image: url("data:img/png;base64,");
