}
.slider-title {
  color: $black;
  font-size: 1.875rem;
  font-weight: 700;
  text-align: left;
  margin-left: 28px;
  margin-top: 5px;
}
.slick-list {
  margin-top: 140px;
}
.slick-prev,
.slick-next {
  position: absolute;
  background-color: $write;
  border: 1px solid $black;
  top: -67px;
  text-indent: -9999px;
  background-repeat: no-repeat;
  background-position: center center;
  display: block;
  width: 10px;
  height: 14px;
  padding: 14px;
  opacity: 0.3;
  transition: 0.2s ease;
  outline: none;
  cursor: pointer;
  &:hover {
    opacity: 1;
  }
}
.slick-next {
  background-image: url("data:img/png;base64,");
  right: 0;
}
.slick-prev {
  background-image: url("data:img/png;base64,");
  right: 47px;
}
.section-apartment {
  margin-bottom: 120px;
  .apartment {
    display: flex;
    flex-wrap: wrap;
  }
}
.slider-child {
