}
.btn-load {
  display: block;
  position: relative;
  margin: 0 auto;
  margin-top: 70px;
  border: none;
  background-color: transparent;
  color: $black;
  outline: none;
  opacity: 0.8;
  transition: 0.2s ease;
  cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    right: -18px;
    background-image: url("data:img/png;base64,");
    background-repeat: no-repeat;
    background-position: 0 3px;
    width: 10px;
    height: 16px;
  }
  &:hover {
    opacity: 1;
  }
}
.section-tabs {
  margin-bottom: 145px;
}
.tabs {
  text-align: center;
  &-button {
    background-color: transparent;
    border: none;
    border-right: 1px solid $black;
    color: $black;
    font-size: 1.12rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 3px;
    padding-right: 55px;
    transition: 0.2s ease;
    opacity: 0.3;
    cursor: pointer;
    &:last-child {
      border-right: none;
      padding-right: 0;
      padding-left: 55px;
    }
    &:hover {
      opacity: 1;
    }
  }
}
.image {
  margin-top: 60px;
}
.info-box {
  margin-bottom: 185px;
  &-list {
    display: flex;
