    &__work {
      span {
        background-image: url("data:img/png;base64,");
        background-size: 58px 56px;
      }
    }
    &__materials {
      span {
        background-image: url("data:img/png;base64,");
        background-size: 58px 54px;
      }
    }
  }
}
.section-news {
