  border: 1px solid $gray;
  width: 460px;
  height: 400px;
  box-sizing: border-box;
  outline: none;
  margin: 0 10px;
  transition: 0.2s ease;
  &:hover::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/news.png);
    background-repeat: no-repeat;
    background-position: content;
    color: $write;
  }
}
.slider-item {
