.header {
  background-image: url(../../../img/bg-1.png);
  background-repeat: no-repeat;
  background-position: cover;
  @media (max-width: 320px) {
    background-image: url(../../../img/mob/bg-mob.png);
    background-repeat: no-repeat;
    background-position: cover;
  }
  &-wrapper {
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 180px;
    @media (max-width: 320px) {
      justify-content: space-around;
      padding-top: 10px;
      margin-bottom: 72px;
    }
  }
  &-dropdown {
    display: inline-block;
    background-image: url(../../../img/burger.svg);
    background-repeat: no-repeat;
    background-position: 0 16px;
    width: 40px;
    height: 40px;
    transition: 0.2s ease;
    cursor: pointer;
    &:hover {
      background-image: url(../../../img/burger-2.svg);
    }
    @media (max-width: 320px) {
      text-indent: -9999px;
      width: 21px;
      height: 21px;
      background-color: $write;
      background-image: url(../../../img/mob/burger.svg);
      background-repeat: no-repeat;
      background-position: center center;
      border-radius: 100%;
      padding: 6px;
    }
  }
  &-item {
    &__maps {
      position: relative;
      margin-left: 154px;
      color: $write;
      &::before {
        content: " ";
        position: absolute;
        top: calc(50% - 11px);
        right: 112px;
        background-image: url("data:img/png;base64,");
        background-repeat: no-repeat;
        background-position: cover;
        width: 16px;
        height: 23px;
      }
    }
    &__tel {
      margin-left: 95px;
      position: relative;
      color: $write;
      &::before {
        content: " ";
        position: absolute;
        top: calc(50% - 10px);
        right: 152px;
        background-image: url("data:img/png;base64,");
        background-repeat: no-repeat;
        background-position: cover;
        width: 21px;
        height: 21px;
      }
    }
    @media (max-width: 320px) {
      text-indent: -9999px;
      width: 21px;
      height: 21px;
      background-color: $write;
      background-image: url("data:img/png;base64,");
      background-repeat: no-repeat;
      background-position: center center;
      border-radius: 100%;
      padding: 6px;
      order: 3;
    }
  }
}
.logo {
  display: inline-block;
  background-image: url(../../../img/HOMEFOR.svg);
  background-repeat: no-repeat;
  background-position: center center;
  width: 206px;
  height: 87px;
  margin-left: 15px;
  margin-right: 31px;
  background-color: $black;
  transition: 0.2s ease;
  cursor: pointer;
  @media (max-width: 320px) {
    background-size: 70px 12px;
    padding: 23px 48px;
    width: 0;
    height: 0;
    order: 2;
    margin-top: -10px;
    margin-left: 0;
  }
  &:hover {
    padding-top: 2px;
  }
}
.nav {
  display: flex;
  list-style-type: none;
  margin-left: 80px;
  &-link {
    margin-right: 50px;
    color: $write;
    transition: 0.2s ease;
    &:hover {
      color: $gray;
    }
  }
  @media (max-width: 320px) {
    display: none;
  }
}
.header-info {
  padding-bottom: 220px;
  @media (max-width: 320px) {
    padding-bottom: 82px;
  }
  &-title {
    max-width: 840px;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 35px;
    color: $write;
    font-size: 3.75rem;
    @media (max-width: 320px) {
      max-width: 300px;
      font-size: 1.25rem;
      line-height: 1.5;
      margin-bottom: 14px;
    }
  }
  &-text {
    max-width: 580px;
    text-align: center;
    margin: 0 auto;
    color: $write;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 60px;
    @media (max-width: 320px) {
      font-size: 0.75rem;
      max-width: 250px;
      margin-bottom: 40px;
    }
  }
  &-button {
    display: block;
    margin: 0 auto;
    color: $black;
    font-weight: 700;
    font-size: 1.12rem;
    letter-spacing: 0.2rem;
    background-color: $write;
    border: none;
    text-decoration: none;
    padding: 28px 53px;
    transition: 0.2s ease;
    outline: none;
    cursor: pointer;
    @media (max-width: 320px) {
      font-size: 0.85rem;
      padding: 18px 30px;
    }
    &:hover {
      color: $gray;
    }
  }
}
