.footer {
  border-top: 1px solid $gray;
  padding-top: 60px;
  &__link {
    margin-right: 10px;
    font-size: 2.12rem;
    font-weight: 500;
    color: $black;
    transition: 0.2s ease;
    cursor: pointer;
    &:hover {
      color: $gray;
    }
  }
}
.box-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  &-info {
    display: flex;
    align-items: center;
  }
}
.footer-logo {
  display: block;
  background-image: url(../../../img/logo.svg);
  background-repeat: no-repeat;
  background-position: center center;
  width: 140px;
  height: 24px;
  margin-right: 50px;
  transition: 0.2s ease;
  cursor: pointer;
}
.menu {
  display: flex;
  justify-content: space-between;
  &-item {
    list-style-type: none;
  }
  &-link {
    margin-left: 50px;
    color: $black;
    transition: 0.2s ease;
    cursor: pointer;
    &:hover {
      color: $gray;
    }
  }
}
.footer-social {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-right: 12px;
  &-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    &__link {
      width: 48px;
      height: 48px;
      background-position: center center;
      background-repeat: no-repeat;
      opacity: 0.8;
      border-radius: 100%;
      display: block;
      border: none;
      background-color: $black;
      transition: 0.2s ease;
      &:hover {
        background-color: $gray;
      }
    }
    &__vk {
      background-image: url("data:img/png;base64,");
      background-size: 23px 13px;
    }
    &__inst {
      margin: 0 30px;
      background-image: url("data:img/png;base64,");
      background-size: 18px 18px;
    }
    &__fb {
      background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAASCAYAAACJgPRIAAAAhElEQVQokeWQMQqDUBBEXyRg4RlSxTq1lVfyAF7Go1hZ6wVCCOmTMoIw8smXTBpN78DC7O5jWQZJXqWkTtJbH93CzoGLLeVQwlcVkFr/Ah7BHG14Nt8DBTCGxi+5hgVYg350kNTHQQ5k0T+Be/TXAGnjUL0FTcDJc2otn8ZD/uvxvUPADH2ic3hM15HMAAAAAElFTkSuQmCC");
      background-size: 9px 16px;
    }
  }
}
.footer-social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  &__link {
    width: 48px;
    height: 48px;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: 0.8;
    border-radius: 100%;
    display: block;
    border: 2px solid $gray;
    transition: 0.2s ease;
    &:hover {
      opacity: 1;
    }
  }

  &__vk {
    background-image: url(../../../img/vk.svg);
    background-size: 33px 23px;
  }
  &__inst {
    background-image: url(../../../img/instagram.svg);
    background-size: 18px 18px;
  }
  &__fb {
    background-image: url(../../../img/facebook.svg);
    background-size: 23px 18px;
  }
}
.footer-form {
  &-input {
    padding: 20px;
    padding-left: 28px;
    transition: 0.2s ease;
    outline: none;
    border: none;
    cursor: pointer;
    &__email {
      width: 490px;
      border: 1px solid $black;
      &:hover {
        border: 1px solid $gray;
      }
      &::placeholder {
        &:focus {
          color: $black;
        }
      }
    }
    &__submit {
      margin-left: -4px;
      width: 220px;
      color: $write;
      background-color: $gray;
      letter-spacing: 3px;
      font-size: 1.12rem;
      font-weight: 700;
      &:hover {
        background-color: $black;
      }
    }
  }
  &__text {
    opacity: 0.8;
  }
}
.box-copytight {
  display: flex;
  justify-content: space-between;
  font-size: 0.75rem;
  color: $gray;
  margin-top: 33px;
  margin-right: 12px;
  margin-bottom: 65px;
  &__link {
    font-size: 0.75rem;
    color: $gray;
    &:hover {
      color: $black;
    }
  }
}
